import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {Subscription} from 'rxjs/Subscription';
import {ActivatedRoute, Params, Router} from '@angular/router';
import { RutasService } from '../../general/utils/rutas.service';
declare interface DataTable {
    headerRow: string[];
    footerRow: string[];
    dataRows: string[][];
}
declare var $, swal: any;
let datatable;
declare var DatatableFunctions: any;
@Component({
    selector: 'app-vistaprevia',
    templateUrl: './comprobante-vista-previa.component.html',
    styleUrls: ['./comprobante-vista-previa.css']
})
export class ComprobanteVistaPreviaComponent implements OnInit, OnDestroy {

    public itemFormGroup: FormGroup;
    public titulo: string;
    editMode = false;
    id: number;
    public data = [
        {
            'cantidad': '1.00',
            'unidadMedida': 'Bobinas',
            'codigo': '123',
            'descripcion': 'Producto de Muestra 1',
            'igv': '2.16',
            'importeItem': '14.16',
        },
        {
            'cantidad': '1.00',
            'unidadMedida': 'Bobinas',
            'codigo': '456',
            'descripcion': 'Producto de Muestra 2',
            'igv': '2.16',
            'importeItem': '14.16',
        },
    ];

    constructor(
                private _rutas: RutasService,
                private route: ActivatedRoute,
                private router: Router) {
                    this.obtenerParametros();
    }

    ngOnInit() {
        cargarDataTable(this.data);
    }
    private obtenerParametros() {
        this.titulo = this.route.snapshot.data['titulo'];
    }
    public regresar() {
        this.router.navigateByUrl( this._rutas.URL_COMPROBANTE_BOLETA_CREAR );
    }

    public emitir() {
        this.router.navigateByUrl( this._rutas.URL_COMPROBANTE_FACTURA_EMITIR );
    }

    ngOnDestroy() {
    }
}

function cargarDataTable(data: Array<any>) {

    datatable = $('#dtresumencomprobantes').DataTable({
        searching: false,
        columns: [
        { data: 'cantidad', name: 'cantidad' },
        { data: 'unidadMedida', name: 'unidadMedida' },
        { data: 'codigo', name: 'codigo' },
        { data: 'descripcion', name: 'descripcion' },
        { data: 'igv', name: 'igv' },
        { data: 'importeItem', name: 'importeItem' },
        ],
        columnDefs: [
        { 'className': 'text-center', 'targets': [0, 1, 2, 3, 4, 5] },
        ],
        data: data
    });
}



