import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { TiposService } from '../utils/tipos.service';
import { GeneralConsultaService } from '../services/general.consulta.service';
import { TipoDocumento } from '../models/tipoDocumento';
import { Subscription } from 'rxjs';
import { RutasService } from '../utils/rutas.service';

declare var $, swal: any;
declare var oGuiaBuscarComponent: ConsultaComponent , datatable;
declare var DatatableFunctions: any;

@Component({
    selector:       'consulta-component',
    templateUrl:    'consulta.component.html',
    styleUrls:      ['./consulta.component.css']
})

export class ConsultaComponent implements OnInit, OnDestroy, AfterViewInit {

    public titulo: string;
    public tipoConsulta: number;
    public tipoComprobanteFlag: boolean;
    public tipoDocumentoFlag: boolean;
    public ticketFlag: boolean;
    public estadoFlag: boolean;
    series:any;
    public consultaFormGroup: FormGroup;
    public tiposComprobantes: Array<Comprobante>;

    public dtoOutListarTipoDocumento: Array<TipoDocumento> = [];

    public subscriptionTipoDocumento: Subscription;
    public data = [
        {
            'Estado': true,
            'Comprobante': '0001-10000001',
            'Serie': '0001',
            'Correlativo': '10000001',
            'ImporteTotal': '10.20',
            'ImporteAUsar': '12.20'
        },
        {
            'Estado': true,
            'Comprobante': '0001-10000001',
            'Serie': '0001',
            'Correlativo': '10000001',
            'ImporteTotal': '10.20',
            'ImporteAUsar': '12.22'
        }
    ];
    constructor(
                    private _route: ActivatedRoute,
                    private _router: Router,
                    private _tipos: TiposService,
                    private _generalConsultaService: GeneralConsultaService,
                    private _rutas: RutasService) {
        this.obtenerParametros();
        this.cargarServiciosArranque();

    }
    ngOnInit() {
        this.InitForm();
        this.cargarServiciosArranque();
        this.setCamposFormulario();
    }
    ngOnDestroy() {
        // this.subscriptionTipoDocumento.unsubscribe();
    }
    private cargarServiciosArranque() {
        
        this.subscriptionTipoDocumento = this._generalConsultaService.listarTipoDocumento()
        .subscribe(
            data => {
                this.dtoOutListarTipoDocumento = data;
                console.log('SERVICIO', this.dtoOutListarTipoDocumento);
            }
        );
    }
    private obtenerParametros() {
        this.titulo = this._route.snapshot.data['titulo'];
        this.tipoConsulta = this._route.snapshot.data['tipoConsulta'];
    }
    private setCamposFormulario() {
        switch ( this.tipoConsulta ) {
            case this._tipos.TIPO_CONSULTA_FACTURA_DOCUMENTO_RELACIONADO:
                    this.tipoComprobanteFlag = true;
                    this.tipoDocumentoFlag = false;
                    this.ticketFlag = false;
                    this.estadoFlag = false;
                    break;
            case this._tipos.TIPO_CONSULTA_BOLETA_DOCUMENTO_RELACIONADO:
                    this.tipoComprobanteFlag = true;
                    this.tipoDocumentoFlag = false;
                    this.ticketFlag = false;
                    this.estadoFlag = false;
                    break;
        }
    }
    private setTipoComprobante() {
        switch ( this.tipoConsulta ) {
            case this._tipos.TIPO_CONSULTA_FACTURA_DOCUMENTO_RELACIONADO:
                    this.tiposComprobantes = [
                        new Comprobante(),
                        new Comprobante()
                    ];
                    this.tiposComprobantes[0].id = this._tipos.TIPO_DOCUMENTO_FACTURA_ANTICIPO;
                    this.tiposComprobantes[0].nombre = this._tipos.TIPO_DOCUMENTO_FACTURA_ANTICIPO_NOMBRE;
                    this.tiposComprobantes[1].id = this._tipos.TIPO_DOCUMENTO_GUIA_REMISION_REMITENTE;
                    this.tiposComprobantes[1].nombre = this._tipos.TIPO_DOCUMENTO_GUIA_REMISION_REMITENTE_NOMBRE;
                    break;
            case this._tipos.TIPO_CONSULTA_BOLETA_DOCUMENTO_RELACIONADO:
                    this.tiposComprobantes = [
                        new Comprobante()
                    ];
                    this.tiposComprobantes[0].id = this._tipos.TIPO_DOCUMENTO_BOLETA_ANTICIPO;
                    break;
        }
    }
    private InitForm() {
        this.consultaFormGroup = new FormGroup({
            'cmbTipoComprobante': new FormControl('', [
                                                        Validators.required
                                                    ]),
            'cmbTipoDocumento': new FormControl('', [
                                                        Validators.required
                                                    ]),
            'txtNumeroDocumento': new FormControl('', [
                                                        Validators.required,
                                                        Validators.pattern('[0-9]+[.][0-9]{2}'),
                                                        Validators.minLength(4),
                                                        Validators.maxLength(16)
                                                    ]),
            'txtTicket': new FormControl('', [
                                                Validators.required,
                                                Validators.pattern('[0-9]+'),
                                                Validators.minLength(1),
                                                Validators.maxLength(20)
                                            ]),
            'cmbSerie': new FormControl('', [
                                                Validators.required
                                            ]),
            'cmbEstado': new FormControl('', [
                                                Validators.required
                                            ]),
            'txtNúmeroCorrelativoInicial': new FormControl('', [
                                                                Validators.required
                                                            ]),
            'txtNúmeroCorrelativoFinal': new FormControl('', [
                                                                Validators.required
                                                            ]),
            'dateFechaEmisionDel': new FormControl('', [
                                                        Validators.required
                                                    ]),
            'dateFechaEmisionAl': new FormControl('', [
                                                        Validators.required
                                                    ])
        });
        this.setTipoComprobante();
    }
    public setTipoDocumentoValidaciones() {

    }

    public buscar() {
        console.log('buscar');
        this._generalConsultaService.buscar();
    }
    ngAfterViewInit() {
        cargarDataTable(this.data);
    }
    public navigate(nav) {
        this._router.navigate(nav, { relativeTo: this._route });
    }
    public regresar() {
        switch ( this.tipoConsulta ) {
            // agregar mas tipos de consulta
            case this._tipos.CONSULTAS_BUSQUEDA_BOLETAS:
                this._router.navigateByUrl( this._rutas.URL_COMPROBANTE_BOLETA_DOCUMENTO_RELACIONADO );
                break;
            case this._tipos.CONSULTAS_BUSQUEDA_FACTURAS:
                this._router.navigateByUrl( this._rutas.URL_COMPROBANTE_FACTURA_DOCUMENTO_RELACIONADO );
                break;
        }
    }
    public showBitacora() {
        swal({
            title: 'Bitácora',
            html: '<div class="col-md-12">' +
                        '<table style="width:100%">' +
                        '<thead>' +
                        '<tr>' +
                          '<th>Fecha</th>' +
                          '<th>Hora</th>' +
                          '<th>Usuario</th>' +
                          '<th>Descripción</th>' +
                        '</tr>' +
                        '</thead>' +
                        '<tbody>' +
                        '<tr style="border:1px">' +
                          '<td>19/10/2017</td>' +
                          '<td>08:05:55</td> ' +
                          '<td>Usuario 1</td>' +
                        '</tr>' +
                        '</tbody>' +
                        '</table>' +
                  '</div>' +
                  '</br>' +
                  '</br>',
            confirmButtonText: 'Regresar'
        });
    }
}

function cargarDataTable(data: Array<any>) {
    datatable = $('#consulta').DataTable({
        searching: false,
        /*serverSide: true,
        ajax: {
            beforeSend: function (request) {
                request.setRequestHeader('Authorization', 'Bearer ' + sessionStorage.getItem('token'));
                request.setRequestHeader('origen_datos', 'PEB2M');
            },
            url: 'http://b2miningdata.com/api/msguialistar/v1/guias/',
            dataSrc: 'data',
            data: d
        },
        columns: [
            {   data: 'Comprobante', name: 'Comprobante' },
            {   data: 'Serie', name: 'Serie' },
            {   data: 'Correlativo', name: 'Correlativo' },
            {   data: 'ImporteTotal', name: 'ImporteTotal' },
            {   data: 'ImporteAUsar', name: 'ImporteAUsar' },
        ],*/
        columns: [
            {   data: 'Estado', name: 'Estado' },
            {   data: 'Comprobante', name: 'Comprobante' },
            {   data: 'Serie', name: 'Serie' },
            {   data: 'Correlativo', name: 'Correlativo' },
            {   data: 'ImporteTotal', name: 'ImporteTotal' },
            {   data: 'ImporteAUsar', name: 'ImporteAUsar' },
        ],
        columnDefs: [
            { 'className': 'text-center', 'targets': [1, 2, 3, 4,5] },
            {
                render: function (data, type, row) {
                    return '<div class="text-right" height="100%"><div class="checkbox text-right">' +
                    '<label><input type="checkbox" name="optionsCheckboxes"></label></div></div>';
                },
                targets: 0,
                orderable: false
            },
        ],
        data: data
    });
    datatable.on('click', '.edit', function (event) {
        let $tr = $(this).closest('tr');
        let row_id=$tr.find( 'a' ).attr('row-id');
        let nav = ['/guia/comprador/formulario', row_id];
        oGuiaBuscarComponent.navigate(nav);
        event.preventDefault();
    });
}
export class Comprobante {
    public id: string;
    public nombre: string;

    constructor() {}
}



